.title {
  color: #fff;
  text-align: left;
  margin-top: 50px;
  margin-left: 130px;
  font-weight: 300;
  font-size: 26px;
}

.ul {
  list-style: none;
  display: flex;
  width: 1000px;
  margin: 0 auto;
  margin-top: 80px;
}

.ul li {
  width: 500px;
}

.navlink {
  text-decoration: none;
  text-align: center;
  color: #6b828f;
  display: block;
  height: 45px;
  font-size: 18px;
  line-height: 45px;
  transition: background-color 1s linear;
  border-top: 1px solid #1e8a71;
}

.navlink:hover {
  background-color: #6b828f18;
}

.navlinkActive {
  color: rgb(208, 224, 231);
  text-decoration: none;
  position: relative;
  border: 1px solid #1e8a71;
}

.filmsUl {
  padding-top: 70px;
  display: flex;
  flex-wrap: wrap;
  text-align: center;
  list-style: none;
}

.filmsSection {
  width: 1000px;
  margin: 0 auto;
}

.filmItem {
  width: 333px;
}

.masonryGridFilm {
  display: flex;
}

.button {
  position: absolute;
  border: none;
  z-index: 10;
  cursor: pointer;
  outline: none;
  box-sizing: border-box;
  background: #cf7a05;
  text-align: center;
  color: #e1e4d9;
  box-shadow: 0px 0px 10px 5px rgb(32, 39, 39);
}

.deleteButton {
  composes: button;
  top: 45px;
  border-radius: 50px;
  right: 30px;
  padding: 10px;
  width: 50px;
  height: 50px;
}

.showMoreButton {
  composes: button;
  left: 50%;
  opacity: 0;
  width: 200px;
  padding: 3px;
  transform: translateX(-50%);
  font-size: 18px;
  font-weight: 400;
  bottom: 120px;
}

.link {
  height: 50px;
  width: 50px;
  display: block;
  text-decoration: none;
}

.img {
  height: 300px;
  width: 200px;
}

.mainWrapper {
  height: 420px;
  position: relative;
}

.mainWrapper:hover .showMoreButton {
  opacity: 1;
}

.filmItemWrapper {
  position: relative;
  margin: 0 auto;
  width: 200px;
  height: 300px;
}

.filmItemWrapper::after {
  content: '';
  opacity: 0;
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 2;
  background: linear-gradient(to right,
      rgba(85, 95, 78, 0.473) 0%,
      rgba(26, 31, 28, 0.24) 100%);
}

.mainWrapper:hover .filmItemWrapper::after {
  opacity: 1;
}

.movieName {
  color: #fff;
  padding-bottom: 7px;
  display: block;
  padding-top: 7px;
  text-align: center;
}

.emptyFilmsDiv {
  color: #fff;
  text-align: center;
  vertical-align: center;
  margin-top: 200px;
}
